import { Component, OnInit } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.css']
})
export class MyProfileComponent implements OnInit {

  profile: any;
  getprofileForm:FormGroup;
  data: any;
  email: any;
  name: any;
  
  mobilenumber: any;
  address: any;
  constructor(private service:OrbisturServiceService,private router:Router, private formbuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.getprofileForm=this.formbuilder.group({
      'name'                                    : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.minLength(2),Validators.pattern("[a-zA-Z ]*")])],
      'email'                                   : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      'mobileNumber'                            : ["",Validators.compose([Validators.required, Validators.maxLength(16),Validators.pattern(/^[1-9][0-9]{9}$/)])],
      'password'                                : ["",[Validators.required,Validators.minLength(8),Validators.maxLength(16),Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i)]],
      'confirmPassword'                         : ["",Validators.required],
      'address'                                 : ["",Validators.compose([Validators.required,Validators.maxLength(256)])],
    })
    this.getProfile();
  }
// *************************Image upload event****************//

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.profile = reader.result;
    console.log("profile",this.profile)
  }


  // ********************get profile Api******************//

  getProfile(){
    this.service.getApi('admin/getProfile',1).subscribe(res=>{
      if(res.status == 200){
        // this.Service.showSuccess('Details have been fetched successfully')
        this.profile=res.body.result.profilePic
        this.data=res.body.result,
        this.email=this.data.email,
        this.name=this.data.firstName,
        this.mobilenumber = this.data.mobileNumber,
        this.address = this.data.address
        console.log("dfsgdfsgdsfg",this.name,this.mobilenumber)
        console.log('name',this.data)
        this.getprofileForm.patchValue({
          'name'                      : this.getprofileForm.value.name,
          'email'                     : this.getprofileForm.value.email,
          'mobileNumber'              : this.getprofileForm.value.mobileNumber,
          'password'                  : this.getprofileForm.value.password,
          'confirmPassword'           : this.getprofileForm.value.confirmPassword,
          'address'                   : this.getprofileForm.value.address,
          'profilePic'                : this.profile
        })
      }
     },error=>{
       this.service.toastErr('Internal server error')
     })
      
  }



  editProfile(){
    let object={
      'name'                      : this.getprofileForm.value.name,
      'email'                     : this.getprofileForm.value.email,
      'mobileNumber'              : this.getprofileForm.value.mobileNumber,
      'password'                  : this.getprofileForm.value.password,
      'confirmPassword'           : this.getprofileForm.value.confirmPassword,
      'address'                   : this.getprofileForm.value.address,
      'profilePic'                : this.profile,
    }
    // console.log('value',this.myProfileForm.value.buddies)
    console.log('object',object)
    this.service.putApi('admin/editProfile',object,1).subscribe(res=>{
      console.log('resEdit===>>',res)
      if(res.body.response_code == 200 ){
      this.profile=res.body.result.profilePic
      // this.service.showSuccess(res.body.response_message)
      this.router.navigate(['dashboard'])
      console.log('profile===>>',this.profile)
      this.getProfile();
      } else
      this.service.toastErr(res.body.response_message) 

    },error=>{
      this.service.toastErr("Internal server error")
    })
  }






// ----------------fafa-icon----------///
  toggleShow() {
    this.show = !this.show;
    if (this.show) {
      this.type = "text";
    }
    else {
      this.type = "password";
    }
  }
type = "password";

  show = false;



}
