// ***************************Image upload 64base******************//


handleInputChange(e) {
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /image-*/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
  alert('invalid format');
  return;
  }
  reader.onload = this._handleReaderLoaded.bind(this);
  reader.readAsDataURL(file);
  }
_handleReaderLoaded(e) {
  let reader = e.target;
  this.imageSrc = reader.result;

  }



  

tabChangedFunc(val) {
  switch (val) {
  case 0:
  this.tab = 'Destination';
  // this.getactivejob();
  break;
  case 1:
  this.tab = 'Website';
  // this.getappliedjob();
  break;
  case 2:
  this.tab = 'Banners';
  // this.getpendingjob();
  break;
  
  }
  }

// ****************************Add Banner Api***********************//

addBanner(){
  this.spinner.show()
  if(this.imageSrc!=null){
  let object={
      'title'       :  this.bannerForm.value.title,
      'bannerPic'   :  this.imageSrc,
      'status'      :   this.bannerForm.value.activebanner
  }
  this.service.postApi('admin/addBanner',object,1).subscribe(res=>{
    if(res.body.response_code==200){

      this.spinner.hide()
      this.addBannerList();
      this.bannerForm.reset()
      this.imageSrc=''
      this.service.showSuccess("Banner has been added successfully.")
      }else if(res.body.response_code==209){
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
      }else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
      }
      },error=>{
      this.spinner.hide()
      this.service.toastErr(error.response_message)
  })

}else{
  this.spinner.hide()
  this.service.toastErr("Please upload banner image.")
}
}


//*************************Add banner List******************************//

addBannerList(){
  this.spinner.show()
  let object={
    'status'      : this.status,
    'pageNumber'  : this.page,
    'limit'       : this.limit,
  }
  this.service.postApi('admin/bannerList',object,1).subscribe(res=>{
    if(res.body.response_code==200){
      this.spinner.hide()
      this.bannerList=res.body.result.docs
      console.log('bannerList',this.bannerList)
      }else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)

      }
  },error=>{
      this.spinner.hide()
      this.service.toastErr(error.response_message)
  })

}

