import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CustomerManagementComponent } from './customer-management/customer-management.component';
import { SubAdminManagementComponent } from './sub-admin-management/sub-admin-management.component';
import { ContentDestinationComponent } from './content-destination/content-destination.component';
import { AddCustomerComponent } from './add-customer/add-customer.component';
import { EditCustomerComponent } from './edit-customer/edit-customer.component';
import { AddSubAdminManagementComponent } from './add-sub-admin-management/add-sub-admin-management.component';
import { AuthGuard } from './auth.guard';
import { EditSubAdminComponent } from './edit-sub-admin/edit-sub-admin.component';
import { EnquriesComponent } from './enquries/enquries.component';
import { EditDestinationComponent } from './edit-destination/edit-destination.component';
import { SettingsPageComponent } from './settings-page/settings-page.component';
import { ViewEncapsulation } from '@angular/compiler/src/core';
import { ViewEnquriesComponent } from './view-enquries/view-enquries.component';
import { EditSettingComponent } from './edit-setting/edit-setting.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { PackageManagementComponent } from './package-management/package-management.component';
import { AddPackageManagementComponent } from './add-package-management/add-package-management.component';
import { ViewPackageManagementComponent } from './view-package-management/view-package-management.component';
import { EditPackageManagementComponent } from './edit-package-management/edit-package-management.component';



const routes: Routes = [
  {path:'',redirectTo:'login',pathMatch:'full'},
  {path:'login',component:LoginComponent},
  {path:'sidebar',component:SidebarComponent,canActivate: [AuthGuard]},
  {path:'forgot-password',component:ForgotPasswordComponent},
  {path:'reset-password/:_id',component:ResetPasswordComponent},
  {path:'dashboard',component:DashboardComponent,canActivate: [AuthGuard]},
  {path:'customer-management',component:CustomerManagementComponent,canActivate: [AuthGuard]},
  {path:'sub-admin-management',component:SubAdminManagementComponent,canActivate: [AuthGuard]},
  {path:'package-management',component:PackageManagementComponent,canActivate:[AuthGuard]},
  {path:'add-package-management',component:AddPackageManagementComponent,canActivate: [AuthGuard]},
  {path:'view-package-management/:id',component:ViewPackageManagementComponent,canActivate: [AuthGuard]},
  {path:'edit-package-management/:_id',component:EditPackageManagementComponent,canActivate: [AuthGuard]},
  {path:'content-destination',component:ContentDestinationComponent,canActivate: [AuthGuard]},
  {path:'add-customer',component:AddCustomerComponent,canActivate: [AuthGuard]},
  {path:'edit-customer/:_id',component:EditCustomerComponent,canActivate: [AuthGuard]},
  {path:'add-sub-admin-management',component:AddSubAdminManagementComponent,canActivate: [AuthGuard]},
  {path:'edit-sub-admin/:_id',component:EditSubAdminComponent,canActivate: [AuthGuard]},
  {path:'enquries',component:EnquriesComponent,canActivate: [AuthGuard]},
  {path:'edit-destination/:id/:val',component:EditDestinationComponent,canActivate: [AuthGuard]},
  {path:'settings-page',component:SettingsPageComponent,canActivate: [AuthGuard]},
  {path:'view-enquries/:id',component:ViewEnquriesComponent,canActivate: [AuthGuard]},
  {path:'edit-setting/:id/:val/:value',component:EditSettingComponent},
  {path:'page-not-found',component:PageNotFoundComponent},
  {path:'my-profile',component:MyProfileComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
