import { Component, OnInit } from '@angular/core';
import { OrbisturServiceService } from '../orbistur-service.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-edit-sub-admin',
  templateUrl: './edit-sub-admin.component.html',
  styleUrls: ['./edit-sub-admin.component.css']
})
export class EditSubAdminComponent implements OnInit {
  profile: any;
  editSubAdminForm:FormGroup;
  ediradmin_id: any;
  adminPatchvalue: any;
  adminPatchvalues: any;
  isValidNumber: any;
  myCode: string;
  constructor(private service:OrbisturServiceService,private router:Router, private formbuilder:FormBuilder,private spinner:NgxSpinnerService,private activate:ActivatedRoute) { }

  ngOnInit() {
    this.activate.params.subscribe(res=>{
      this.ediradmin_id=res._id
    })
    this.editSubAdminForm=this.formbuilder.group({
      'name'                                    : ['',Validators.compose([Validators.required, Validators.maxLength(60),Validators.minLength(2),Validators.pattern("[a-zA-Z ]*")])],
      'email'                                   : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      'number'                                  : ["",Validators.required],
      'password'                                : ['',[Validators.required,Validators.minLength(8),Validators.maxLength(16),Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i)]],
      'confirmPassword'                         : ['',Validators.required],
      'address'                                 : ['',Validators.required],
      'dashboard'                               : [false],
      'customerManagement'                      : [false],
      'subAdminManagement'                      : [false],
      'packageManagement'                       : [false],
      'bookingManagement'                       : [false],
      'transferManagement'                      : [false],
      'sightseeingManagement'                   : [false],
      'transactionManagement'                   : [false],
      'visaManagement'                          : [false],
      'contentManagement'                       : [false],
      'inquiryManagement'                       : [false],
      'supportManagement'                       : [false],
      'settingManagement'                       : [false],
    })
    this.viewAdmin()
    this.phoneCheckCountry()
  }
// *************************mobile number method**************//
phoneCheckCountry() {
  $("#phoneNumber").intlTelInput({
    autoPlaceholder: false,
    autoFormat: false,
    autoHideDialCode: false,
    initialCountry: 'in',
    nationalMode: false,
    onlyCountries: [],
    // preferredCountries: ["us"],
    formatOnInit: true,
    separateDialCode: true,
    formatOnDisplay: false
  });
}
toCheckSpaceChar() {
  this.isValidNumber = $('#phoneNumber').intlTelInput('isValidNumber');
  const countryData = $('#phoneNumber').intlTelInput('getSelectedCountryData');
  this.myCode = "+" + countryData.dialCode;

}
// *************************Image upload event****************//

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.profile = reader.result;
    console.log("profile",this.profile)
  }



// ***************************View Sub Admin Api***************************//



viewAdmin(){
this.service.getApi('admin/viewSubAdmin/'+this.ediradmin_id,1).subscribe(res=>{
if(res.body.response_code==200){
  this.spinner.hide()
  this.service.showSuccess(res.body.response_message)
  this.adminPatchvalues=res.body.result
  console.log('adminPatchvalue==>>',this.adminPatchvalues)
  this.editSubAdminForm.patchValue({
    'name'                            : this.adminPatchvalues.name,
    'email'                           : this.adminPatchvalues.email,
    'number'                          : this.adminPatchvalues.mobileNumber,
    'address'                         : this.adminPatchvalues.address,
    'dashboard'                       : this.adminPatchvalues.permissions[0].dashboard,
    'customerManagement'              : this.adminPatchvalues.permissions[0].customerManagement,
    'subAdminManagement'              : this.adminPatchvalues.permissions[0].subAdminManagement,
    'packageManagement'               : this.adminPatchvalues.permissions[0].packageManagement,
    'bookingManagement'               : this.adminPatchvalues.permissions[0].bookingManagement,
    'transferManagement'              : this.adminPatchvalues.permissions[0].transferManagement,
    'sightseeingManagement'           : this.adminPatchvalues.permissions[0].sightseeingManagement,
    'transactionManagement'           : this.adminPatchvalues.permissions[0].transactionManagement,
    'visaManagement'                  : this.adminPatchvalues.permissions[0].visaManagement,
    'contentManagement'               : this.adminPatchvalues.permissions[0].contentManagement,
    'inquiryManagement'               : this.adminPatchvalues.permissions[0].inquiryManagement,
    'supportManagement'               : this.adminPatchvalues.permissions[0].supportManagement,
    'settingManagement'               : this.adminPatchvalues.permissions[0].settingManagement,
    'profilePic'                      : this.adminPatchvalues.permissions[0].profilePic
  })
}
else{
  this.spinner.hide()
  this.service.toastErr(res.body.response_message)
}
},error=>{
  this.spinner.hide()
  this.service.toastErr("Internal server error")
})
}

  // **************************Add Sub Admin Api****************************//
  editSubAdmin(){
  this.spinner.show()
  let object ={
    'subAdminId'                      : this.adminPatchvalues._id,
    'name'                            : this.editSubAdminForm.value.name,
    'email'                           : this.editSubAdminForm.value.email,
    'mobileNumber'                    : this.editSubAdminForm.value.number,
    'password'                        : this.editSubAdminForm.value.password,
    'confirmPassword'                 : this.editSubAdminForm.value.confirmPassword,
    'address'                         : this.editSubAdminForm.value.address,
    'dashboard'                       : this.editSubAdminForm.value.dashboard,
    'customerManagement'              : this.editSubAdminForm.value.customerManagement,
    'subAdminManagement'              : this.editSubAdminForm.value.subAdminManagement,
    'packageManagement'               : this.editSubAdminForm.value.packageManagement,
    'bookingManagement'               : this.editSubAdminForm.value.bookingManagement,
    'transferManagement'              : this.editSubAdminForm.value.transferManagement,
    'sightseeingManagement'           : this.editSubAdminForm.value.sightseeingManagement,
    'transactionManagement'           : this.editSubAdminForm.value.transactionManagement,
    'visaManagement'                  : this.editSubAdminForm.value.visaManagement,
    'contentManagement'               : this.editSubAdminForm.value.contentManagement,
    'inquiryManagement'               : this.editSubAdminForm.value.inquiryManagement,
    'supportManagement'               : this.editSubAdminForm.value.supportManagement,
    'settingManagement'               : this.editSubAdminForm.value.settingManagement,
    'profilePic'                      : this.profile
  }
    // console.log('dashboard==>>',object)
    console.log('valid',this.editSubAdminForm.valid)
    this.service.putApi('admin/editSubAdmin',object,1).subscribe(res=>{
    if(res.body.response_code==200){
      this.spinner.hide()
      this.service.showSuccess("Sub-admin has been updated successfully.")
      this.router.navigate(['sub-admin-management'])
    }
    else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
    }
    },error=>{
      this.spinner.hide()
      this.service.toastErr("Internal server error")
    })
    }
}
