import { Component, OnInit } from '@angular/core';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  constructor(private router:Router,private service:OrbisturServiceService,private spinner:NgxSpinnerService) { }

  ngOnInit() {
  }
  logout(){
    this.spinner.show()
    // $("#modallogout").modal("hide");
    $('#modallogout').modal({ backdrop: 'static', keyboard: false })
    localStorage.removeItem('token')
    this.service.showSuccess('Logout successfully')
    setTimeout(
      function(){ 
        window.location.reload();
        this.spinner.hide()

      },100);
    this.router.navigate(['login'])
    
  }


}
