import { Component, OnInit } from '@angular/core';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { CsvServiceService } from '../csv-service.service';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
declare var $:any;
@Component({
  selector: 'app-sub-admin-management',
  templateUrl: './sub-admin-management.component.html',
  styleUrls: ['./sub-admin-management.component.css']
})
export class SubAdminManagementComponent implements OnInit {
 
  limit=10;
  total: any;
  page=1;
  p = 0;
  user_id: any;
  block_id: any;
  filterName:string;
  dataArr:any=[];
  subAdminLists: any=[];
  status: any;
  subAdmin: any;
  isValidNumbers: any;
  myCode: string;
  isValidNumber: any;
 

  constructor(private service:OrbisturServiceService,private router:Router,private formBuilder:FormBuilder,private csvServiceService:CsvServiceService,private spinner:NgxSpinnerService) { 
   this.subAdminList();
  }

  ngOnInit() {

  }

  // *******************Mobile number methods*************************//





  //********************Search by name event**************************//

  searchValue(value){
        this.filterName=value
        console.log('filterName==>>', this.filterName)
        this.subAdminLists = []
        this.subAdminList();
        }

//********************Reset Method******************************//
reset(){
        this.filterName='';
       this.subAdminList()   
}
 
 //********************Sub Admin list Api**************************//
subAdminList(){ 
       this.spinner.show()
        let object={
        "search"          : this.filterName,
        "page"            : this.page,
        "limit"           : this.limit,
        }
        this.service.postApi('admin/subAdminList',object,1).subscribe(res=>{
        console.log('res==>>',res) 
        if(res.body.response_code==200){
        this.spinner.hide() 
        // this.subAdminLists=res.body.result.docs?res.body.result.docs:res.body.result  
        this.subAdminLists=res.body.result.docs; 
        this.total=res.body.result.total
        console.log('total==>>>',this.total)
        }else{
        this.spinner.hide()
        // this.service.toastErr(res.body.response_message)          
        }
        },error=>{
        this.spinner.hide()
        this.service.toastErr(error.response_message)    
        })
        }

        changePage(page) {
          console.log('Page ', page)
          this.page = page;
          this.p = page - 1;
         this.subAdminList()  
          }

          statusblock(){
            this.subAdminLists =[];
            // console.log('subAdminLists',this.subAdminLists)
            this.subAdminLists.forEach(items=>{
              if(items.status=="ACTIVE"){
                this.subAdminLists.push(items)
              }
              console.log('subAdminListsItems',this.subAdminLists)
            })
          }



        // ***********************csv*********************//

        download(){
        let dataArr = [];
        this.subAdminLists.forEach((element,ind) => {
        dataArr.push({
        "S no":ind+1,
        "Name":element.name?element.name:'',
        "Email address":element.email?element.email:'N/A',
        "Mobile number":element.mobileNumber?element.mobileNumber:'N/A',
        "Registration on":element.createdAt?element.createdAt.slice(0,10):'N/A',
        "Status":element.status=='ACTIVE'?'Active':'Inactive',   
        })
          }) 
          console.log('download=>',dataArr)
          this.csvServiceService.downloadFile(dataArr, 'dataArr');
        }

// **************************Get id method for delete Sub Admin*******************************//
  deleteFunction(id){
        this.user_id=id
        console.log('user_id',this.user_id)
        $('#modaldelete').modal({ backdrop: 'static', keyboard: false })
        }
// ***************************Delete method*******************************//
deleteFunctions(){  
        this.spinner.show()
        this.service.deleteApi('admin/deleteSubAdmin/'+this.user_id,1).subscribe(res=>{
        if(res.body.response_code == 200){
        this.subAdminList()
        this.spinner.hide()
        this.service.showSuccess("Sub-admin has been deleted successfully.")
        this.subAdminList()
        }else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
        }
        },error=>{
        this.spinner.hide()
        this.service.toastErr("Internal server error")        
        })
        }




// +***************************Sub-Admin Block and Unblock Method*******************//
      block(_id,status){
        this.block_id=_id
        this.status=status
        console.log('block_id==>>',this.block_id)
        console.log('status==>>',this.status)
        console.log('block_id',this.block_id,status)
         $('#modalblock').modal({ backdrop: 'static', keyboard: false })
        }

        blockFunction(){
          let object={
          "subAdminId"  : this.block_id
          }
          console.log('hhhhhhh======>',this.block_id)
          this.service.postApi('admin/blockUnblockSubAdmin',object,1).subscribe(res=>{
          console.log('res_block',res)
          if(res.body.response_code == 200){
          this.service.showSuccess(res.body.response_message)
          this.subAdminList()
          }else{
          this.service.toastErr(res.body.response_message)
          }
          })
          }
}
