import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { CsvServiceService } from '../csv-service.service';

@Component({
  selector: 'app-edit-package-management',
  templateUrl: './edit-package-management.component.html',
  styleUrls: ['./edit-package-management.component.css']
})
export class EditPackageManagementComponent implements OnInit {
  addpackageForm : FormGroup;
  store: any=[];
  id: string;
  profile: any;
  editorValue: string = '';

  constructor(private service:OrbisturServiceService,private router:Router,private formBuilder:FormBuilder,private csvServiceService:CsvServiceService,private spinner:NgxSpinnerService, public param:ActivatedRoute) 
  {
    this.param.params.subscribe(success=>{
      console.log("succeghdfgss",success)
      this.id = success._id
    })
   }

  ngOnInit() {
    this.formvalidation();
    this.viewData();
  }


  // *************************Image upload event****************//

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.profile = reader.result;
    console.log("profile", this.profile)
  }

  formvalidation() {
    this.addpackageForm = this.formBuilder.group({
      'country': [''],
      'destination': [''],
      'packagetype': [''],
      'packagename': [''],
      'packagedays': [''],
      'packagenight': [''],
      'packagedescription': [''],
      'Itinery': [''],

      'Packageinclusions': [''],

      'Exclusions': [''],

      'Terms': [''],
      'Packagecost': [''],
      'Cancellationcharges': [''],
      'flightsincluded': [''],
      'Hotelsincluded': [''],
      'Transfersincluded': [''],
      'Transferscategory': [''],
      'Transferstype': [''],
      'Cartype': [''],
      'Sightseeingincluded': [''],
      'Ownername': [''],
      'Ownercontact': [''],
      'Pricepernight': [''],
      'Status': [''],
      'active': [''],
      'Inactive': [''],


    })
  }
  viewData() {

    this.service.getApi('admin/viewPackage/'+this.id,1).subscribe(res=>{
      console.log("hhhhhhhhh=======>",res)
       this.store =res.body.result
      // console.log('ress===>>',this.store)
      if(res.body.response_code==200){
        this.spinner.hide()
        this.service.showSuccess(res.body.response_message)
        this.store=res.body.result
        console.log('viewvalue===========>>',this.store)
        this.addpackageForm.patchValue({
          "packagePicture":this.profile,
          "country":this.store.country,
        "destinationId":this.store.countryId,
        "packageTypeId":this.store.packagetype,
        "transferTypeId":[this.store.Transferstype],
        "transferCategoryId":[this.store.Transferscategory],
        "carTypeId":[this.store.Cartype],
        "packagename":this.store.country,
        "packageDays":this.store.packagedays,
        "packageNights":this.store.packagenight,
        "packageDescription":this.editorValue,
        "flightsIncluded":this.store.value.flightsincluded,
        "hotelsIncluded":this.store.value.Hotelsincluded,
        "transferIncluded":this.store.value.Transfersincluded,
        "sightseeingIncluded":this.store.value.Sightseeingincluded,
        "ownerName":this.store.value.Ownername,
        "ownerContact":this.store.value.Ownercontact,
        "pricePerNight":this.store.value.Pricepernight,
        "itinery":[{'arrive':"9",'meal':"9",'description':'jhb'},{'arrive':"0",'meal':"9",'description':'kjj'}],
        "exclusions": [this.store.value.Exclusions],
        "packageInclusion":[this.store.value.Packageinclusions],
        "termsAndConditions":this.store.value.Terms,
        "packageCost":[{'hotelCategory':"9",'hotelName':'ads','pricePerAdult':1950},
                       {'hotelCategory':"9",'hotelName':'ads','pricePerAdult':1950}],
        "cancellationCharge":[this.store.value.Cancellationcharges],
       
        })
      }
      else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }
      },error=>{
        this.spinner.hide()
        this.service.toastErr("Internal server error")
      })
      }

       // **************************edit-package-management Api****************************//
  editpackage(){
    this.spinner.show()
    let object ={
      "packageId":this.id,
      "packagePicture":this.profile,
      "countryId":this.addpackageForm.value.country,
    "destinationId":this.addpackageForm.value.destination,
    "packageTypeId":this.addpackageForm.value.packagetype,
    "transferTypeId":[this.addpackageForm.value.Transferstype],
    "transferCategoryId":[this.addpackageForm.value.Transferscategory],
    "carTypeId":[this.addpackageForm.value.Cartype],
    "packageName":this.addpackageForm.value.packagename,
    "packageDays":this.addpackageForm.value.packagedays,
    "packageNights":this.addpackageForm.value.packagenight,
    "packageDescription":this.editorValue,
    "flightsIncluded":this.addpackageForm.value.flightsincluded,
    "hotelsIncluded":this.addpackageForm.value.Hotelsincluded,
    "transferIncluded":this.addpackageForm.value.Transfersincluded,
    "sightseeingIncluded":this.addpackageForm.value.Sightseeingincluded,
    "ownerName":this.addpackageForm.value.Ownername,
    "ownerContact":this.addpackageForm.value.Ownercontact,
    "pricePerNight":this.addpackageForm.value.Pricepernight,
    "itinery":[{'arrive':"9",'meal':"9",'description':'jhb'},{'arrive':"0",'meal':"9",'description':'kjj'}],
    "exclusions": [this.addpackageForm.value.Exclusions],
    "packageInclusion":[this.addpackageForm.value.Packageinclusions],
    "termsAndConditions":this.addpackageForm.value.Terms,
    "packageCost":[{'hotelCategory':"9",'hotelName':'ads','pricePerAdult':1950},
                   {'hotelCategory':"9",'hotelName':'ads','pricePerAdult':1950}],
    "cancellationCharge":[this.addpackageForm.value.Cancellationcharges],
   
    }
      // // console.log('dashboard==>>',object)
      // console.log('valid',this.editSubAdminForm.valid)
      this.service.postApi('​admin​/editPackage',object,1).subscribe(res=>{
      if(res.body.response_code==200){
        this.spinner.hide()
        this.service.showSuccess("Package-Management has been updated successfully.")
        this.router.navigate(['package-management'])
      }
      else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }
      },error=>{
        this.spinner.hide()
        this.service.toastErr("Internal server error")
      })
      }
   
   
  
 
}
