  selectStatus(value){
    this.status=value
    console.log('staatus==>>',this.status)
    if((this.status=='ACTIVE')||(this.status=='INACTIVE')){
      this.packageList();
      this.getContentList();
      this.addBannerList();
      this.cartypeList();
      this.countryDataList();
      this.getTransferLiat();
      this.getTransferTypeLiat();
    }else if(this.status=='ALL'){
      // this.packageList();
      this.filterName = '';
      this.page =1;
      this.limit = 10;
      this.status = null;
      setTimeout(()=>{
        this.packageList();
        this.getContentList()
        this.addBannerList();
        this.cartypeList();
        this.countryDataList()
        this.getTransferLiat();
        this.getTransferTypeLiat();
      },100)
    }
  }

