// ***************************Image upload 64base******************//


handleInputChange(e) {
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /image-*/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
  alert('invalid format');
  return;
  }
  reader.onload = this._handleReaderLoaded.bind(this);
  reader.readAsDataURL(file);
  }
_handleReaderLoaded(e) {
  let reader = e.target;
  this.imageSrc = reader.result;

  }



  

tabChangedFunc(val) {
  switch (val) {
  case 0:
  this.tab = 'PackageTypes';
  // this.getactivejob();
  break;
  case 1:
  this.tab = 'Transfer';
  // this.getappliedjob();
  break;
  case 2:
  this.tab = 'CarType';
  // this.getpendingjob();
  break;
  case 3:
  this.tab = 'Countries';
  
  }
  }


  tabChangedFuncs(val) {
    switch (val) {
    case 0:
    this.tabs = 'TransferCategory';
    // this.getactivejob();
    break;
    case 1:
    this.tabs  = 'TransferType';
    // this.getappliedjob();
    break;
  
    
    }
    }

// ****************************Add Banner Api***********************//

addBanner(){
  this.spinner.show()
  let object={
      'title'       :  this.bannerForm.value.title,
      'bannerPic'   :  this.imageSrc,
      'status'      :   this.bannerForm.value.activebanner
  }
  this.service.postApi('admin/addBanner',object,1).subscribe(res=>{
    if(res.body.response_code==200){
      this.spinner.hide()
      this.addBannerList();
      this.service.showSuccess(res.body.response_message)
      }else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
      }
  },error=>{
      this.spinner.hide()
      this.service.toastErr(error.response_message)
  })

}


