import { Component, OnInit } from '@angular/core';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  resetPasswordForm: FormGroup;
  id: any;
  show2: boolean = false;
  show1: boolean = false;
  type: any = "password";

  show: boolean = false;
  type2: string = "password";
  type1: string = "password";

  constructor(private service: OrbisturServiceService, private router: Router, private activeRouter: ActivatedRoute, private fb: FormBuilder, private spinner: NgxSpinnerService) { }
  ngOnInit() {
    this.activeRouter.params.subscribe(res => {
      this.id = res._id
      console.log('id=>', this.id)
    })
    this.resetPasswordForm = this.fb.group({
      'newPassword': ['', Validators.compose([Validators.required, Validators.minLength(8), Validators.maxLength(16)])],
      'confirmPassword': ['', Validators.required]
    })
  }
// ********************Reset Password Api****************************//
 
  reserPassword() {
    this.spinner.show()
    let object = {
      "newPassword": this.resetPasswordForm.value.newPassword,
      "confirmPassword": this.resetPasswordForm.value.confirmPassword
    }
    this.service.postApi('admin/resetPassword/'+this.id,object, 0).subscribe(res => {
        if (res.status == 200) {
        this.spinner.hide()
        this.service.showSuccess(res.body.response_message)
        this.router.navigate(['login'])
        } else {
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
        }
        }, error => {
        this.spinner.hide()
        this.service.toastErr(error.response_message);
        })
        }
  toggleShow1() {
        this.show = !this.show;
        if (this.show) {
        this.type = "text";
        }
        else {
        this.type = "password";
        }
        }
  toggleShow2() {
        this.show2 = !this.show2;
        if (this.show2) {
        this.type2 = "text";
        }
        else {
        this.type2 = "password";
        }
        }
}
