import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {
  profile: any;
  addCustomerForm:FormGroup;
  isValidNumber: any;
  myCode: string;
  constructor(private service:OrbisturServiceService,private router:Router, private formbuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.addCustomerForm=this.formbuilder.group({
      'name'                                    : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.minLength(2),Validators.pattern("[a-zA-Z ]*")])],
      'email'                                   : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      'number'                                  : ["",Validators.compose([Validators.required, Validators.maxLength(16),Validators.pattern(/^[1-9][0-9]{9}$/)])],
      'password'                                : ["",[Validators.required,Validators.minLength(8),Validators.maxLength(16),Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i)]],
      'confirmPassword'                         : ["",Validators.required],
      'address'                                 : ["",Validators.compose([Validators.required,Validators.maxLength(256)])],
    })
    this.phoneCheckCountry();
  }

// *************************mobile number method**************//
phoneCheckCountry() {
  $("#phoneNumber").intlTelInput({
    autoPlaceholder: false,
    autoFormat: false,
    autoHideDialCode: false,
    initialCountry: 'in',
    nationalMode: false,
    onlyCountries: [],
    // preferredCountries: ["us"],
    formatOnInit: true,
    separateDialCode: true,
    formatOnDisplay: false
  });
}
toCheckSpaceChar() {
  this.isValidNumber = $('#phoneNumber').intlTelInput('isValidNumber');
  const countryData = $('#phoneNumber').intlTelInput('getSelectedCountryData');
  this.myCode = "+" + countryData.dialCode;

}
// *************************Image upload event****************//

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.profile = reader.result;
    console.log("profile",this.profile)
  }


  // ********************Add Customer Api******************//

  addCustomer(){
   console.log('addCustomerForm==>>>',this.addCustomerForm.valid)
    this.spinner.show()
    let coutomerObject={
      'name'                      : this.addCustomerForm.value.name,
      'email'                     : this.addCustomerForm.value.email,
      'mobileNumber'              : this.addCustomerForm.value.number,
      'password'                  : this.addCustomerForm.value.password,
      'confirmPassword'           : this.addCustomerForm.value.confirmPassword,
      'address'                   : this.addCustomerForm.value.address,
      'profilePic'                : this.profile,
    }
    this.service.postApi('admin/addCustomer',coutomerObject,1).subscribe(res=>{
        if(res.body.response_code == 200){
        this.spinner.hide()
        this.service.showSuccess("Customer has been added successfully.")
        this.router.navigate(['customer-management'])
        }else{
          this.spinner.hide()
        this.service.toastErr(res.body.response_message)
        }
        },error=>{
          this.spinner.hide()
        this.service.toastErr(error.response_message)        
        })
  }

}
