import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-edit-customer',
  templateUrl: './edit-customer.component.html',
  styleUrls: ['./edit-customer.component.css']
})
export class EditCustomerComponent implements OnInit {

  editCustomerForm:FormGroup;
  customer_id: any;
  patchvalues: any;
  profile: any;
  isValidNumber: any;
  myCode: string;
  constructor(private service:OrbisturServiceService,private router:Router, private formbuilder:FormBuilder,private activate:ActivatedRoute,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.editCustomerForm=this.formbuilder.group({
      'name'                                    : ['',Validators.compose([Validators.required, Validators.maxLength(60),Validators.minLength(2),Validators.pattern("[a-zA-Z ]*")])],
      'email'                                   : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      'number'                                  : ["",Validators.compose([Validators.required, Validators.maxLength(16),Validators.pattern(/^[1-9][0-9]{9}$/)])],
      'password'                                : ['',[Validators.minLength(8),Validators.maxLength(16),Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i)]],
      'confirmPassword'                         : [''],
      'address'                                 : ["",Validators.compose([Validators.required,Validators.maxLength(256)])],
    })
      this.activate.params.subscribe(res=>{
      this.customer_id=res._id
      console.log('customer_id==>>',this.customer_id)
    })
    this.viewCustomer()
    this.phoneCheckCountry()
  }



  // *************************mobile number method**************//
phoneCheckCountry() {
  $("#phoneNumber").intlTelInput({
    autoPlaceholder: false,
    autoFormat: false,
    autoHideDialCode: false,
    initialCountry: 'in',
    nationalMode: false,
    onlyCountries: [],
    // preferredCountries: ["us"],
    formatOnInit: true,
    separateDialCode: true,
    formatOnDisplay: false
  });
}
toCheckSpaceChar() {
  this.isValidNumber = $('#phoneNumber').intlTelInput('isValidNumber');
  const countryData = $('#phoneNumber').intlTelInput('getSelectedCountryData');
  this.myCode = "+" + countryData.dialCode;

}

// ****************************View customer id*************************//
  viewCustomer(){  
    let object={
      "customerId"    : this.customer_id
    }
    this.service.postApi('admin/viewCustomer',object,1).subscribe(res=>{
    if(res.body.response_code == 200){
    this.spinner.hide()
    this.patchvalues=res.body.result
    console.log('patchvalues==>>',this.patchvalues.name)
    this.editCustomerForm.patchValue({
      'name'                : this.patchvalues.name,
      'email'               : this.patchvalues.email,
      'number'              : this.patchvalues.mobileNumber,
      'address'             : this.patchvalues.address,
    })
    
    }else{
    this.service.toastErr(res.body.response_message)
    }
    },error=>{
    this.service.toastErr("Internal server error")        
    })
    }
    

// ***********************Image upload event************************//

handleInputChange(e) {
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /image-*/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
    alert('invalid format');
    return;
  }
  reader.onload = this._handleReaderLoaded.bind(this);
  reader.readAsDataURL(file);
}
_handleReaderLoaded(e) {
  let reader = e.target;
  this.profile = reader.result;
  console.log("profile",this.profile)
}

// ***********************Edit Customer Api*************************//

editCustomer(){
  this.spinner.show()
   let object={
    'customerId'          : this.customer_id,
    'name'                : this.editCustomerForm.value.name, 
    'email'               : this.editCustomerForm.value.email,
    'mobileNumber'        : this.editCustomerForm.value.number,
    'password'            : this.editCustomerForm.value.password,
    'confirmPassword'     : this.editCustomerForm.value.confirmPassword,
    'address'             : this.editCustomerForm.value.address,
    'profilePic'          : this.profile
   }
   console.log('object==>>',object)
   this.service.putApi('admin/editCustomer',object,1).subscribe(res=>{
    console.log('resss==>>',res)
    if(res.body.response_code==200){
      this.spinner.hide()
      this.service.showSuccess("Customer has been updated successfully.")
      this.router.navigate(['customer-management'])
    }
    else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
    }
    },error=>{
      this.spinner.hide()
      this.service.toastErr(error.response_message)
    })
    }
}



