import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { CsvServiceService } from '../csv-service.service';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-enquries',
  templateUrl: './enquries.component.html',
  styleUrls: ['./enquries.component.css']
})
export class EnquriesComponent implements OnInit {
  customerForm:FormGroup;
  enqLists: any;
  limit=10;
  total: any;
  page:any=1;
  p = 0;
  paginationData: any = { limit: 10, page: 1, total: 0 };
  user_id: any;
  block_id: any;
  filterName:string;
  dataArr:any=[];
  enquiryList: any;
  id: any;
  searchname: any=[];
  search: string;
  
  length: any;
 

  constructor(private service:OrbisturServiceService,private router:Router,private formBuilder:FormBuilder,private csvServiceService:CsvServiceService,private spinner:NgxSpinnerService) { 
    
  }

  ngOnInit() {
    this.enqList()
  
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
     this.enqList()
    }

  
//********************Reset Method******************************//
reset(){
  this.searchname = ''
  this.search='';
  this.enqList()    
}
 
 //********************Customer list Api**************************//
enqList(){ 
  console.log('in enqList');
       this.spinner.show()
        let object={
        "search"    : this.searchname,
        }
        this.service.postApi('admin/inquiryList',object,1).subscribe(res=>{
        // console.log('res==>>',res) 
        if(res.body.response_code==200){
        this.spinner.hide() 
        // this.service.showSuccess(res.body.response_message) 
        // this.enqLists=res.body.result.docs?res.body.result.docs:res.body.result  
        this.length = res.body.result;
        this.enquiryList=res.body.result.docs; 
        this.paginationData.total = this.length.length; 
        console.log("hsdfgdsfg",this.enquiryList)
        this.total=res.body.result.total
        }else if(res.body.response_code == 404){
          this.spinner.hide();
          this.enquiryList = res.body.result
          console.log("asdasdasdasdas",this.enquiryList)
        }
        else{
        this.spinner.hide() 
        this.service.toastErr(res.body.response_message)        
        }
        },error=>{
        this.spinner.hide()
        this.service.toastErr(error.response_message)    
        })
        }

        changePage(page) {
        console.log('Page ', page)
        this.page = this.page;
        this.p = page-1;
        this.enqList()   
        }
 

// **************************Get id method for delete customer*******************************//
  deleteFunction(id){
        this.user_id=id
         console.log('user_id',this.user_id)
        $('#modaldelete').modal({ backdrop: 'static', keyboard: false })
        
        }
// ***************************Delete method*******************************//
  deleteFunctions(){  
        this.service.deleteApi('admin/deleteInquiry/'+this.user_id,1).subscribe(res=>{
          console.log("del", res)
        if(res.body.response_code == 200){
        this.enqList()
        this.spinner.hide()
        this.service.showSuccess(res.body.response_message)
        this.enqList()
        }else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
        }
        },error=>{
        this.spinner.hide()
        this.service.toastErr("Internal server error")        
        })
        }


        // gotoview(id){
        //   console.log("")
        //   this.router.navigate(['/view-enquries'],{queryParams:{id:id}})
        // }


}
