import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OrbisturServiceService } from '../orbistur-service.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  reserpasswordForm:FormGroup
  constructor(private service:OrbisturServiceService,private router:Router, private fb:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
      this.reserpasswordForm=this.fb.group({
      "email":["",Validators.compose([Validators.required,Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])]
      })
      }

  // ******************Forgot Password Api****************//
  forgotPassword(){
    this.spinner.show()
    let object={
    "url"    : this.service.websiteUrls+'reset-password',
    "email"  : this.reserpasswordForm.value.email
   }
   console.log('object',object)
   this.service.postApi('admin/forgotPassword',object,0).subscribe((res)=>{
    if(res.body.response_code == 200){
    this.spinner.hide()
    this.service.showSuccess(res.body.response_message)
    this.router.navigate(['login'])
    }else{
    this.spinner.hide()
    this.service.toastErr(res.body.response_message)
    }
    },error=>{
    this.spinner.hide()
    this.service.toastErr(error.response_message)
    })
    }
    }
